import PageHeader from "../../components/PageHeader";
import DataTable from "./component/DataTable";
import useCore from "../../hooks/useCore";
import {useGetActiveChainId} from "../../state/chains/hooks";
import TextWrapper from "../../components/TextWrapper";
import React, {useMemo} from "react";
import CountDownDiv from "../ParticularV3Pool/components/CountDownDiv";
import {GaugeRewardsStartTime} from "../ParticularV3Pool";
import InfoBox from "./component/InfoBox";
import useGetV3StakingAPR from "../../hooks/state/V3Staking/useGetV3StakingAPR";

const UniswapV3Gauges = () => {
  const core = useCore();
  const chainId = useGetActiveChainId();
  const gauges = core.getGauges(chainId);
  const apr = useGetV3StakingAPR();

  const dataWithApr = useMemo(() => {
    return gauges.map(data => {
      var minApr = '-';
      var maxApr = '-';
      if (!apr.isLoading && apr.data[data.apiKey]) {
        minApr = apr.data[data.apiKey].min;
        maxApr = apr.data[data.apiKey].max;
      }
      return {...data, minApr: minApr, maxApr: maxApr}
    })

  }, [apr.data, apr.isLoading, gauges])

  return (
    <div className={'custom-container'}>
      <PageHeader
        title={'Uniswap V3 MAHA Staking'} subTitle={'Earn MAHA rewards by staking your Uniswap V3 LP tokens'}
        className={'m-b-40'} showLearnMore
        learnMoreLink="https://docs.mahadao.com/governance/pool-incentives/uniswap-v3-staking"/>
      <div>
        {GaugeRewardsStartTime > new Date().getTime() && <div className={'m-b-24'}>
          <CountDownDiv/>
        </div>}
        <div className={'m-b-24'}>
          <InfoBox/>
        </div>
        <DataTable data={dataWithApr}/>
        <div className={'m-t-24'}>
          <TextWrapper
            text={<div>
              To register your own pool for gauges and pool incentives, create a governance proposal <a
              href={'https://discuss.mahadao.com/c/governance/gauge-proposals/54'} target={'_blank'}
              className={'links'}>here</a>
            </div>}
            align={'center'}
          />
        </div>
      </div>
    </div>
  )
}

export default UniswapV3Gauges;
