import {Redirect, Route, Switch} from "react-router-dom";

import Page from "./components/Page/Page";
import Home from "./views/Home";
import Locker from "./views/Locker";
import ComingSoon from "./components/ComingSoon";
import NoPageFound from "./components/NoPageFound";
import Faucet from "./views/Faucet";
import config from "./config";
import GaugeWeightVote from "./views/GaugeWeightVote";
import Rewards from "./views/Rewards";
import Bribes from "./views/Bribes";
import {useGetActiveChainId} from "./state/chains/hooks";
import UniswapV3Gauges from "./views/UniswapV3Gauges";
import ParticularV3Pool from "./views/ParticularV3Pool";
import MAHAXRewards from "./views/MAHAXRewards";
import Staking from "./views/Staking";
import Referral from "./views/Referral";
import ReferralOnboarding from "./views/ReferralOnboarding";
import JoiningDAO from "./views/ReferralOnboarding/JoiningDAO";

const Navigation = () => {
  const chainId = useGetActiveChainId();

  if (chainId === 137) {
    return (
      <Switch>
        <Route exact path="/">
          <Home/>
        </Route>
        <Route exact path="/locker">
          <Page>
            <Locker/>
          </Page>
        </Route>
        <Route exact path="/mahaxRewards">
          <Page>
            <MAHAXRewards/>
          </Page>
        </Route>
        <Route exact path="*">
          <Redirect to="/"/>
        </Route>
      </Switch>
    )
  }

  return (
    <Switch>
      <Route exact path="/">
        <Home/>
      </Route>
      <Route exact path="/rewards">
        <Page>
          <Rewards/>
        </Page>
      </Route>
      <Route exact path="/referral">
        <Page>
          <Referral/>
        </Page>
      </Route>
      <Route exact path="/referral/onboarding">
        <Page>
          <ReferralOnboarding/>
        </Page>
      </Route>
      <Route exact path="/referral/onboarding/join-dao">
        <Page>
          <JoiningDAO/>
        </Page>
      </Route>
      <Route exact path="/staking/:poolId">
        <Page availableNetworks={[137, 1337]}>
          <Staking/>
        </Page>
      </Route>
      <Route exact path="/v3Staking/:poolAddress">
        <Page>
          <ParticularV3Pool/>
        </Page>
      </Route>
      <Route exact path="/v3Staking">
        <Page>
          <UniswapV3Gauges/>
        </Page>
      </Route>
      <Route exact path="/pool-incentives">
        <Page>
          <Bribes/>
        </Page>
      </Route>
      <Route exact path="/delegation">
        <Page>
          <ComingSoon/>
        </Page>
      </Route>
      <Route exact path="/pools-vote">
        <Page>
          <GaugeWeightVote/>
        </Page>
      </Route>
      <Route exact path="/locker">
        <Page>
          <Locker/>
        </Page>
      </Route>
      <Route exact path="/gauge-boost">
        <Page>
          <ComingSoon/>
        </Page>
      </Route>
      {config[chainId].chainId !== 137 && <Route exact path="/faucet">
        <Page>
          <Faucet/>
        </Page>
      </Route>}
      <Route exact path="*">
        <NoPageFound/>
      </Route>
      <Redirect to="/"/>
    </Switch>
  );
}

export default Navigation;
